import React from 'react'
import Link from 'gatsby-link'

import { Container, Col, Row } from 'reactstrap'
import styled from 'styled-components'

const HeaderContainer = styled(Container)`
  background-color: #000000;
  font-family: 'Ubuntu', sans-serif;
`
const Title = styled.h1`
  font-size: 26px;
`

const ContactList = styled.ul`
  font-size: 18px;
`

const Header = ({ siteTitle }) => (
  <HeaderContainer className="p-5" fluid={true}>
    <Row>
      <Col xs="5">
        <Title>
          <Link
            to="/"
            style={{
              color: 'white',
              textDecoration: 'none',
            }}
          >
            <span className="text-secondary">{siteTitle}</span> an interesting space
          </Link>
        </Title>
      </Col>
      <Col xs={{ size: 5, offset: 2 }}>
        <ContactList className="list-unstyled list-inline text-secondary">
          <li className="list-inline-item" style={{color: 'white'}}>Contact</li>
          <li className="list-inline-item">425.894.1716</li>
          <li className="list-inline-item">skyler@shapesocial.net</li>
        </ContactList>
      </Col>
    </Row>
  </HeaderContainer>
)

export default Header



